.domroot {
    width: 0.001vw;
    height: 0.001vh;
    position: fixed;
    z-index: 99999;
    overflow-y: scroll;
    overflow-x: hidden;
    transition: width 0.2s ease-out, height 0.2s ease-out;
    
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;

    font-family: 'Roboto', sans-serif;


    gap: 20px;

}

.domroot.active{
    width: 55vw;
    height: 60vh;
}

.domroot.evenmoreactive{
    width: 95vw;
    height: 90vh;
}

.domroot.evenmoreactive #modelViewer{
    height: 60vh;
}

.iosBlur{
    backdrop-filter: blur(33px);
    background-blend-mode: overlay;
    box-shadow: 0 10px 15px rgb(0 0 0 / 20%);
    background-color: #1a1a1a7d;  
    border-radius: 12px; 
}

.closeButton{
    padding-left: 12px;
    padding-right: 12px;
    padding-top: 4px;
    padding-bottom: 4px;
    color: rgba(255, 255, 255, 0.562);
    display: flex;
    font-weight: 900;
    overflow: hidden;
    flex: 0 0 auto;
}

.absolute{
    position: absolute;
    left: 10px;
}

.row{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.first {
    justify-content: space-between;
}

.brand{
    color: #ffffffc9;
    font-weight: 600;
    font-size: 20px;
    letter-spacing: 1.5px;   
    justify-self: center;
    flex: 0 0 auto;
}

.productname{
    color: #ffffff85;
    font-weight: 400;
    font-size: 18px;
}

.productDesc{
    width: 100%;
    color: #ababab;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.productDesc > * {
    margin: 0px;
    padding: 0px;
}

.viewonsite {
    background-color: #0A84FF;
    color: white;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 9px;
    padding-bottom: 9px;
    border-radius: 15px; 
}

.sutbutt{
    
    border: none;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 9px 12px;
    border-radius: 20px;
    right: 20%;
    bottom: 10%;
    position: absolute;

}

.sutbutt svg{
    /* stroke: #ffffff; */
    fill: #ffffff;
}